import React, { useState, useEffect } from "react";
import ExitIcon from '../subcomponents/exit'


function Portfolio() {
  const [gifState, setGifState] = useState([false, false, false, false]);

  const handleGif = (event) => {
    let gifStates = gifState;
   
    if (event.type === "mouseenter" && gifStates[event.target.id] === false){
      gifStates[event.target.id] = true;
    } else if (event.type === "mouseleave" && gifStates[event.target.id] === true){
      gifStates[event.target.id] = false;
    }
    
    setGifState([...gifStates]);
  };

  const openSocial = (url) => {
    const newWindow = window.open(url, '_blank', 'noopener,noreferrer')
    if (newWindow) newWindow.opener = null
  }

  return (
    <>
    <div className="section-background portfolio">
      <div className="section-top" style={{}}>
        <h2 className="section-header">My Projects</h2>
        <ExitIcon/>
      </div>

      <div className="projects">
        <div className="project">
          <div className="project-header">
            <h2 onClick={() => openSocial('https://pantojao.github.io/nasa-app/#/')} className="project-title middle">Nasa Image Generator</h2>
            <img onClick={() => openSocial('https://github.com/pantojao/nasa-app')}  className="github-icon" src = {require('../media/github.png')} alt=""/>
          </div>
          <img className={gifState[0] === false ? "project-image" : "project-video"} id={0} src={ gifState[0] === false ? require("../media/nasa-app-pic.PNG") : require("../media/nasa-app-vid.png") } alt="Nasa App" onMouseEnter={(event) => handleGif(event)} onMouseLeave={(event) => handleGif(event)} />
        </div>

        <div className="project">
          <div className="project-header">
            <h2 onClick={() => openSocial('https://pantojao.github.io/Pac-Man-Search/')} className="project-title middle">Pac-Man Search</h2>
            <img onClick={() => openSocial('https://github.com/pantojao/pac-man-search')} className="github-icon"  src = {require('../media/github.png')} alt=""/>
          </div>
          <img className={gifState[1] === false ? "project-image" : "project-video"} id={1} src={ gifState[1] === false ? require("../media/pac-man-image.PNG") : require("../media/pac-man-search.png") } alt="Weather App" onMouseEnter={(event) => handleGif(event)} onMouseLeave={(event) => handleGif(event)} />
        </div>





        
        <div className="project">
          <div className="project-header">
            <h2 onClick={() => openSocial('https://pantojao.github.io/weather-app/')} className="project-title middle">Current Weather Report</h2>
            <img onClick={() => openSocial('https://github.com/pantojao/weather-app')} className="github-icon"  src = {require('../media/github.png')} alt=""/>
          </div>
          <img className={gifState[1] === false ? "project-image" : "project-video"} id={1} src={ gifState[1] === false ? require("../media/weather-app-pic.PNG") : require("../media/weather-app-vid.png") } alt="Weather App" onMouseEnter={(event) => handleGif(event)} onMouseLeave={(event) => handleGif(event)} />
        </div>

        <div className="project">
          <div className="project-header">
            <h2 onClick={() => openSocial('https://pantojao.github.io/ipa-app/')} className="project-title middle">IPA Finder</h2>
            <img onClick={() => openSocial('https://github.com/pantojao/ipa-app')} className="github-icon"  src = {require('../media/github.png')} alt=""/>
          </div>
          <img className={gifState[2] === false ? "project-image" : "project-video"} id={2} src= { gifState[2] === false ? require("../media/ipa-app-pic.PNG") : require("../media/ipa-app-vid.png") } alt="IPA App" onMouseEnter={(event) => handleGif(event)} onMouseLeave={(event) => handleGif(event)} />
        </div>

        <div className="project">
          <div className="project-header">
            <h2 onClick={() => openSocial('https://gainz-trackerr.herokuapp.com/')} className="project-title middle">Gainz Tracker</h2>
            <img onClick={() => openSocial('https://github.com/pantojao/gainz-tracker')} className="github-icon"  src = {require('../media/github.png')} alt=""/>
          </div>
          <img className={gifState[3] === false ? "project-image" : "project-video"} id={3} src={gifState[3] === false ? require("../media/gainz-app-pic.PNG") : require("../media/gainz-app-vid.png") } alt="Gainz Tracker" onMouseEnter={(event) => handleGif(event)} onMouseLeave={(event) => handleGif(event)} />
        </div>       
      </div>
    </div>
    </>
  );
}

export default Portfolio;
